/*    06. Alerts     */
/*********************/

#DonorInputCheck {
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

#PatientInputCheck {
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

#PledgeInputCheck {
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.alert {
  width: 100%;
  padding: 10px;
  margin: 10px;
  opacity: 1;
  transition: opacity 0.6s;
}

.alert.warning {
  /* padding: 1px; */
  font-weight: 600;
  background: #e69100;
  display: none;
}

.closebtn {
  color: white;
  float: right;
  transform: translateY(-100%);
  cursor: pointer;
  transition: 0.3s;
}

.closebtn:hover {
  color: black;
}

/*************************/
/*     07. Preloader     */
/*************************/

.spinner {
  position: fixed;
}

.spinner>div {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  border-radius: 100%;
  background-color: #0079a8;
  text-align: center;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {

  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
  }

  40% {
    -webkit-transform: scale(1.0);
  }
}

@keyframes sk-bouncedelay {

  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    transform: scale(0);
  }

  40% {
    -webkit-transform: scale(1.0);
    -ms-transform: scale(1.0);
    transform: scale(1.0);
  }
}